export default {
	data() {
		return {
			title: 'picker',
			array: ['100km','50km','20km','10km','5km','1km'],
			index: 0,
			name : "",
			shop : [],
			types:100,
			ard:this.ard,
			url:this.url1
		}
	},
	onLoad(options) {
	   this.name = options.name;
	   this.getArr()
	},
	methods: {
		go:function(item){
			uni.navigateTo({
				url:"../dianPu/dianPu?item="+JSON.stringify(item)
			})
		},
		getArr:function () {
			let that=this;
			let usurl2=that.url1+"/dinghai/shop/bytype";
			uni.showLoading({
				title: '正在获取',
				mask: true
			});
			uni.request({
			    url: usurl2,
				header:{
					"Content-Type" : "application/x-www-form-urlencoded"
				},
				method:'GET',
			    data: {
					type:'装修',
					Longitude:that.ard.longitude,
					Latitude:that.ard.latitude,
					distance:that.types
				},
			    success: (res) => {
					if(res.statusCode==200){
						uni.showToast({
							title: '加载成功'
						});
						that.shop=res.data.flag
					}else{
						uni.showToast({
							title: '您的网络打了个盹！',
							icon:"none"
						});
					}
			    },
				fail : function(err){
					console.log(err)
					uni.showToast({
						title: '您的网络打了个盹！',
						icon:"none"
					});
				}
			})
		},
		back : function(){
		   uni.navigateBack({

		   });
		},
		getDate(type) {
		            const date = new Date();
		            let year = date.getFullYear();
		            let month = date.getMonth() + 1;
		            let day = date.getDate();
		
		            if (type === 'start') {
		                year = year - 60;
		            } else if (type === 'end') {
		                year = year + 2;
		            }
		            month = month > 9 ? month : '0' + month;;
		            day = day > 9 ? day : '0' + day;
		            return `${year}-${month}-${day}`;
		        },
		bindPickerChange: function(e) {
		            let i=e.target.value;
		             e.target.value;
		            if(i==0){
		            	this.types = 100;
		            }else if(i==1){
		            	this.types = 50
		            }else if(i==2){
		            	this.types = 20
		            }else if(i==3){
		            	this.types = 10
		            }else if(i==4){
		            	this.types = 5
		            }else {
		            	this.types = 1
		            }
					this.getArr()
		        }
	}
}
